<template>
  <el-header class="header">
    <!--前进后退按钮-->
    <span class="navigation" @click="goBack" :class="{disabled: $store.state.index===0}">
      <span class="el-icon-arrow-left" style="font-weight: 1000;color: var(--deepText)"/>
    </span>
    <span class="navigation" @click="goForward"
          :class="{disabled: $store.state.index === $store.state.visitedRoutes.length-1}">
      <span class="el-icon-arrow-right" style="font-weight: 1000;color: var(--deepText)"/>
    </span>
    <div style="margin-left: auto;display: flex;flex-direction: row">
      <!--个人中心-->
      <span @click="$router.push('/mine')" class="navigation"
            :style="$route.path.endsWith('/mine') ? 'background-color: var(--btn);padding: 0 3px 0 9px' : 'padding: 0 3px 0 9px'">
