                  p-id="6620" fill="currentColor"
                  :color="$route.path.endsWith('/mine') ? 'var(--btnText)' : 'var(--deepText)'"></path></svg>
        </span>
      <!--信息通知-->
      <el-popover
          placement="bottom"
          :append-to-body="false"
          :close-delay="0"
          width="400"
          trigger="hover"
          ref="pop">
        <span
            style="display: flex;justify-content: center;font-weight: bold;font-size: 20px;letter-spacing: 3px">消息列表</span>
        <el-divider/>
        <div v-for="msg in messageList" :key="msg.id" style="display: flex;flex-direction: column">
          <span style="font-weight: lighter;margin-bottom: 15px">{{ timeFormat(msg.createTime) }}</span>
          <span style="letter-spacing: 2px">{{ msg.name }}申请成为{{ msg.careHomeName }}的{{ role(msg.role) }}</span>
          <div style="margin-top: 10px">
            <span class="bot" :style="botStyle(msg.status)"></span>
            <span style="font-weight: bold">{{ msg.status === null ? '待审核' : msg.status === 1 ? '已通过' : '已拒绝' }}</span>
          </div>
          <el-divider/>
        </div>
        <div style="margin: 30px auto 50px;font-size: 15px;font-weight: lighter;width: 70px;" v-if="messageList.length===0">
          暂无消息
        </div>
        <span @click="showAll" class="floatingBtn">查看全部</span>
        <span slot="reference" @click="$router.push('/message')" class="navigation"
              :style="$route.path.endsWith('/message') ? 'background-color: var(--btn);padding: 18px 7px 0 7px' : 'padding: 18px 7px 0 7px'">
          <svg style="font-weight: bolder;padding: 18px 0 2px 0" t="1691739734866" class="icon" viewBox="0 0 1024 1024"
