              fill="currentColor" p-id="2744"
              :color="$route.path.endsWith('/message') ? 'var(--btnText)' : 'var(--deepText)'"></path></svg>
        </span>
      </el-popover>
      <!--退出登录-->
      <span @click="exit" class="navigation" style="padding: 0 2px 0 10px">
          <svg style="font-weight: bolder;padding: 9px 0 0 0" t="1690970902120" class="icon" viewBox="0 0 1024 1024"
               version="1.1" xmlns="http://www.w3.org/2000/svg"
