              fill="currentColor" color="var(--deepText)" p-id="3577"></path><path
              d="M434.97772373 864.6885376h-243.57546666v-703.41973333h243.57546666c23.52633173 0 42.5984-19.07206827 42.5984-42.5984s-19.07206827-42.5984-42.5984-42.5984h-286.17386666c-23.52633173 0-42.5984 19.07206827-42.5984 42.5984v788.61653333c0 23.52633173 19.07206827 42.5984 42.5984 42.5984h286.17386666c23.52633173 0 42.5984-19.07206827 42.5984-42.5984s-19.07206827-42.5984-42.5984-42.5984z"
              fill="currentColor" color="var(--deepText)" p-id="3578"></path></svg>
        </span>
      <!--切换主题-->
      <span @click="changeTheme" class="navigation" style="padding: 0 3px 0 9px">
          <span v-if="currentTheme" class="el-icon-sunny"
                style="font-size: 20px;font-weight: 1000;color: var(--deepText)"></span>
          <span v-else class="el-icon-moon" style="font-size: 20px;font-weight: 1000;color: var(--deepText)"></span>
        </span>
    </div>

  </el-header>
</template>

<script>
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Header",
  data() {
    return {
      //当前主题
      currentTheme: localStorage.getItem('theme') == null ? false : JSON.parse(localStorage.getItem('theme')),
      //获取到的消息列表
      messageList: [],
      //用户信息
      user: {}
    }
  },
  methods: {
    //前进后退按钮方法
    goBack() {
      this.$store.commit("goBack")
    },
    goForward() {
      this.$store.commit("goForward")
    },
    //切换主题
    changeTheme() {
      //如果未保存过则直接存入false，即黑色主题
      if (localStorage.getItem('theme') == null) {
        localStorage.setItem('theme', 'false')
        this.$store.commit("setTheme")
        this.currentTheme = false
      } else {
        //如果保存过，则直接取反
        this.$store.commit("setTheme")
        let value = !JSON.parse(localStorage.getItem('theme').toString())
        localStorage.setItem('theme', value)
        this.currentTheme = value
      }
    },
    //退出登录
    exit() {
      localStorage.removeItem('user')
      this.$router.push('/login')
    },
    //根据用户role属性判断角色
    role(r) {
      if (r === 1) {
        return '管理员'
      } else if (r === 0) {
        return '老人'
      } else if (r === 2) {
        return '护工'
      } else {
        return '家属'
      }
    },
    //根据Date字符串返回可供显示的时间
    timeFormat(time) {
      let date = new Date(time)
      return date.getFullYear() + ' ' + this.zeroTool(date.getMonth() + 1) + '.' + this.zeroTool(date.getDate()) + ' ' + this.zeroTool(date.getHours()) + ':' + this.zeroTool(date.getMinutes());
    },
    //自动补零工具
    zeroTool(s) {
      s = s.toString()
      if (s.length === 1) {
        return '0' + s
      }
      return s
    },
    //获取消息列表
    getList() {
      this.request.get('/identity-verification/listByName', {
        params: {
          name: this.user.careHomeName
        }
      }).then(res => {
        if (res.code === '200') {
          //获取数据
          this.messageList = res.data.slice(0, 3)
        } else {
          this.$notify({
            type: 'error',
            title: '获取消息列表错误',
            message: res.msg
          })
        }
      }).catch(err => {
        this.$notify({
          type: 'error',
          title: '获取消息列表错误',
          message: err
        })
      })
    },
    //跳转至信息页面
    showAll() {
      if(this.$route.path !== '/message'){
        this.$router.push('/message')
      }
      this.$refs.pop.doClose()
    },
    //样式颜色判断
    botStyle(status) {
      if (status === null) {
        return 'background: var(--btn)'
      } else {
        return status === 1 ? 'background: #25d120' : 'background: #ff6060'
      }
    },
  },
  mounted() {
    //先获取用户信息，如果没有就不做轮询
    let user = localStorage.getItem('user')
    if (user == null) {
      return
    }
    //如果存在用户信息，则为已登录状态，进行轮询
    this.user = JSON.parse(user)
    // //轮询消息列表
    this.getList()
    this.timer = window.setInterval(() => {
      setTimeout(() => {
        this.getList()
      }, 0)
    }, 5000)
  },
  destroyed() {
    window.clearInterval(this.timer)
  }
}
</script>

<style scoped>
.header {
  position: fixed;
  z-index: 1;
  height: 60px;
  display: flex;
  font-size: 20px;
  /*background-color: #006bad;*/
  border-bottom: solid 1px rgba(64, 64, 64, 0);
  width: 82%;
  padding-right: 18px;
}

.navigation {
  border-radius: 50%;
  background-color: var(--inputBg);
  width: 26px;
  height: 38px;
  margin: 10px 8px 10px 0;
  line-height: 40px;
  font-size: 15px;
  padding-left: 12px;
  cursor: pointer;
}

.navigation:hover {
  background-color: var(--btnHover);
}

.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.disabled:hover {
  background-color: var(--inputBg);
}

/*弹出框样式*/
>>> .el-popover {
  background-color: var(--lightBg);
  color: var(--deepText);
  margin-right: 10px;
  border: 1px solid var(--inputBorder);
}

>>> .popper__arrow {
  display: none;
}

/*悬浮窗按钮*/
.floatingBtn {
  display: flex;
  margin: -10px auto 5px;
  justify-content: center;
  padding: 5px;
  background-color: var(--hover);
  width: 200px;
  border-radius: 5px;
  cursor: pointer;
}

.floatingBtn:hover {
  background-color: var(--btnHover);
}

/*圆点*/
.bot {
  display: inline-block;
  height: 10px;
  width: 10px;
  border-radius: 5px;
  background-color: black;
  margin-right: 8px;
}
</style>